import Estpa6 from './pagina6.module.css'
import saltea1 from '../assets/img/saltea1.jpeg'
import saltea2 from '../assets/img/saltea2.jpg'
import saltea3 from '../assets/img/saltea3.jpg'
import saltea4 from '../assets/img/saltea4.jpg'
import saltea5 from '../assets/img/saltea5.jpg'
import saltea6 from '../assets/img/saltea6.jpg'
import saltea7 from '../assets/img/saltea7.jpg'

import Encabezado from "../componentes/encabezado";
export const Pagina6 =() => {
    return (
        <div className={Estpa6.Pag6}>
            <Encabezado titulo="LA SALTEA"/>
            <body className={Estpa6.cuerpo}>
                
                <div className={Estpa6.imagenes}>
                    <div className={Estpa6.imagen}>
                    <img src={saltea1}
                        width="100%"
                        height="100%"/>
                    </div>
                    <div className={Estpa6.imagen}>
                    <img src={saltea2}
                        width="100%"
                        height="100%"/>
                    </div>
                    <div className={Estpa6.imagen}>
                    <img src={saltea3}
                        width="100%"
                        height="100%"/>
                    </div>
                </div>
                <div className={Estpa6.contsec}>
                    <section className={Estpa6.seccion}>
                    <img src={saltea4}
                        width="100%"
                        height="100%"/>
                    </section>
                </div>
                <div className={Estpa6.imagenes}>
                    <div className={Estpa6.imagen}>
                    <img src={saltea5}
                        width="100%"
                        height="100%"/>
                    </div>
                    <div className={Estpa6.imagen}>
                    <img src={saltea6}
                        width="100%"
                        height="100%"/>
                    </div>
                    <div className={Estpa6.imagen}>
                    <img src={saltea7}
                        width="100%"
                        height="100%"/>
                    </div>
                </div>
            </body>
        </div>
        
    );
}