import Estpa3 from './pagina3.module.css'
import Encabezado from '../componentes/encabezado';
import paseo1 from '../assets/img/paseo1.jpg'
import paseo2 from '../assets/img/paseo2.jpg'
import paseo3 from '../assets/img/paseo3.jpg'
import paseo4 from '../assets/img/paseo4.jpg'
import paseo5 from '../assets/img/paseo5.jpg'
import paseo6 from '../assets/img/paseo6.jpg'
export const Pagina3 =() => {
    return (
        <div className={Estpa3.Pag3}>
            <Encabezado titulo="Plato paseo"/>
            <body className={Estpa3.cuerpo}>
                
                <div className={Estpa3.contsec}>
                    <div className={Estpa3.imagen}>
                    <img src={paseo1}
                        width="100%"
                        height="100%"/>
                    </div>
                    <section className={Estpa3.seccion}>
                    <img src={paseo2}
                        width="100%"
                        height="100%"/>
                    </section>
                    <div className={Estpa3.imagen}>
                    <img src={paseo3}
                        width="100%"
                        height="100%"/>
                    </div>
                    <div className={Estpa3.imagen}>
                    <img src={paseo4}
                        width="100%"
                        height="100%"/>
                    </div>
                    <section className={Estpa3.seccion}>
                    <img src={paseo5}
                        width="100%"
                        height="100%"/>
                    </section>
                    <div className={Estpa3.imagen}>
                    <img src={paseo6}
                        width="100%"
                        height="100%"/>
                    </div>
                </div>
            </body>
        </div>
    ) ;
}